import { Component, OnInit } from '@angular/core';
import { CreateRivistaService } from '../create-rivista/create-rivista.service';
import { Rivista } from 'src/app/model/Rivista';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Pagina } from 'src/app/model/Pagina';
import { Puzzle } from 'src/app/model/puzzle';
import { CreatePuzzleComponent } from '../create-puzzle/create-puzzle.component';
import { CreatePuzzleService } from '../create-puzzle/create-puzzle.service';

@Component({
  selector: 'app-add-page',
  templateUrl: './add-page.component.html',
  styleUrls: ['./add-page.component.css']
})
export class AddPageComponent implements OnInit {

  rivista: Rivista;

  constructor(
    private rivistaService: CreateRivistaService,
    private puzzleService: CreatePuzzleService
  ) { 
    this.rivista = rivistaService.rivista;

    console.log(this.rivista)
  }

  ngOnInit() {
  }

  paginaForm = new FormGroup({
    page: new FormControl('', Validators.required),
    row: new FormControl('', Validators.required),
    col: new FormControl('', Validators.required),
    title: new FormControl('', Validators.required)
  })

  onSubmit(){
    console.log(this.paginaForm.getRawValue());
    var obs = this.puzzleService.createPuzzle(this.paginaForm.getRawValue());
    
    let created: Puzzle;
    obs.subscribe(c => {
      created = c.data 
      let pagina:Pagina = {
        nPagina: this.paginaForm.getRawValue().page,
        puzzles:[created],
        id:undefined
      }
      this.rivista.pages = [];
      this.rivista.pages.push(pagina);
      this.rivistaService.updateRivista(this.rivista);
    });

  }

}
